import { Fragment, useEffect } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import Table from "./Table"
import DropDown from "./DropDown"
import { Row, Col, Card } from "reactstrap"
import StatsHorizontal from "@components/widgets/stats/StatsHorizontal"
import { User, UserPlus, UserCheck, UserX } from "react-feather"
import Icon from "@mui/material/Icon"
import AddCardOutlinedIcon from "@mui/icons-material/AddCardOutlined"
import "./style.scss"
import { selectThemeColors } from "@utils"
import classnames from "classnames"
import Select from "react-select"
import {
  storeName,
  getAlwaysGrid,
  getApiSummaryData,
  getSummaryData
} from "./store"
import { Skeleton } from "@mui/material"

const List = ({
  lookupMode = false,
  lookupMode_fieldName = "",
  lookupMode_dropdown = false,
  lookupMode_defaultValue,
  lookupMode_onChange,
  lookupMode_isMulti = false,
  errorState = false
}) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  useEffect(() => {
    if (
      getApiSummaryData &&
      (store.dataSummary?.length === 0 ||
        (!store.isLoadingSummary && getAlwaysGrid))
    ) {
      dispatch(getSummaryData({}))
    }
  }, [dispatch, store.dataSummary?.length])

  const colors = [
    "primary",
    "success",
    "warning",
    "danger",
    "info",
    "secondary"
  ]

  return !lookupMode_dropdown ? (
    <Fragment>
      <h3>{t("Permissions List")}</h3>
      <p>
        {t(
          "Each category (Basic, Professional, and Business) includes the four predefined roles shown below."
        )}
      </p>
      {!lookupMode && getApiSummaryData && (
        <Row>
          {store.dataSummary?.map((item, index) => {
            return (
              <Col>
                <StatsHorizontal
                  color={colors[index]}
                  statTitle={`${t(item.title)}`}
                  icon={
                    item.icon ? (
                      <Icon
                        baseClassName="material-icons-outlined"
                        fontSize="20"
                      >
                        {item.icon}
                      </Icon>
                    ) : (
                      <Icon
                        baseClassName="material-icons-outlined"
                        fontSize="20"
                      >
                        lightbulb
                      </Icon>
                    )
                  }
                  renderStats={
                    store.isLoadingSummary ? (
                      <Skeleton
                        sx={{ bgcolor: "#eef2f8" }}
                        animation="wave"
                        height={25}
                        width={40}
                        className="fw-bolder mb-75"
                      />
                    ) : (
                      <h3 className="fw-bolder mb-75">
                        {item.count_format ?? item.count}
                      </h3>
                    )
                  }
                />
              </Col>
            )
          })}
        </Row>
      )}

      <Card>
        <div className="card-datatable app-user-list table-responsive">
          <Table
            lookupMode={lookupMode}
            lookupMode_fieldName={lookupMode_fieldName}
            lookupMode_defaultValue={lookupMode_defaultValue}
            lookupMode_onChange={lookupMode_onChange}
            lookupMode_isMulti={lookupMode_isMulti}
          />
        </div>
      </Card>
    </Fragment>
  ) : (
    <DropDown
      lookupMode_fieldName={lookupMode_fieldName}
      lookupMode_onChange={lookupMode_onChange}
      lookupMode_defaultValue={lookupMode_defaultValue}
      lookupMode_isMulti={lookupMode_isMulti}
      errorState={errorState}
    />
  )
}

export default List
